@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Montserrat:100,100i,200,200i,300,300i,400,500,600,600i,700,700i);
.main-div {
  background-color: #ec2c47;
  width: 100%;
  padding: 3.5vh 0; }
  .main-div h1 {
    color: white;
    font-size: 80px;
    font-weight: 500; }
  .main-div h1.main-div-text {
    padding-top: 5%; }
  .main-div .main-div-button-set {
    margin: 5% 0; }
  @media (max-width: 370px) {
    .main-div h2 {
      font-size: 7vw; } }

.cards-clients {
  padding: 2vh 0;
  background-color: white;
  color: white; }
  .cards-clients .card1 {
    margin-top: 3vh;
    margin-bottom: 3vh; }
  .cards-clients .card-title {
    color: #ec2c47; }
  .cards-clients .text-below a {
    color: black; }
  .cards-clients .text-below a:hover {
    color: #4066ff; }
  .cards-clients .card-img-top {
    background-color: #ec2c47; }
  .cards-clients .heading {
    text-align: center;
    font-weight: 500;
    font-size: 4em;
    color: black; }

.Services {
  background-color: white;
  color: black;
  padding: 3vh 0; }
  .Services .heading {
    text-align: center;
    font-weight: 500;
    font-size: 4em;
    color: black; }
  .Services .row {
    margin: 4vh 0; }
  .Services .Services-cards {
    box-shadow: none; }
    .Services .Services-cards .card-title {
      font-size: calc(1.8vw);
      font-weight: 600; }
    .Services .Services-cards .card-text {
      font-size: calc(1.4vw);
      font-weight: 400; }
  @media only screen and (max-width: 600px) {
    .Services .Services-cards .card-title {
      font-size: 1em;
      font-weight: 600; }
    .Services .Services-cards .card-text {
      font-size: 1em;
      font-weight: 400; } }
  @media only screen and (max-width: 1300px) and (min-width: 601px) {
    .Services .Services-cards .card-title {
      font-size: 1.5em;
      font-weight: 600; }
    .Services .Services-cards .card-text {
      font-size: 1.3em;
      font-weight: 400; } }

.page-footer {
  background-color: #ec2c47 !important;
  font-family: Montserrat, sans-serif; }
  .page-footer p {
    line-height: 1; }
  .page-footer .credit-contents {
    position: absolute;
    bottom: 0;
    font-size: 0.7em; }
  .page-footer .footer-copyright a {
    text-decoration: underline; }
  .page-footer .list-unstyled {
    line-height: 1.8; }

.form {
  background-color: #ec2c47;
  height: 55vh; }
  .form ::label {
    color: white; }

.text-white {
  color: white; }

.form-main {
  background-color: white; }

.map {
  width: auto;
  background-color: #ec2c47; }

input::-webkit-input-placeholder, input::-webkit-input-placeholder {
  color: white; }

input::placeholder, input::-webkit-input-placeholder {
  color: white; }

form label {
  color: white !important; }

.forms {
  padding: 3vh 0; }

* {
  margin: 0;
  padding: 0; }

body {
  color: white;
  background-color: #ec2c47;
  font-family: "Montserrat", sans-serif; }

@media (min-width: 1500px) {
  .container {
    max-width: 1300px; } }

